import type { Suggestion } from '../types';
import { Inline, Action } from '@ali/4ever-cangjie';

import {
  MENTION_ACTIVE,
  activeMention,
  isActiveMention,
} from './activeMention';

import {
  MENTION_SELECT,
  selectMention,
  isSelectMention
} from './selectMention';

import {
  MENTION_PLACEHOLDER_ACTIVE,
  activePlaceholder,
  isActivePlaceholder
} from './activePlaceholder';

export {
  MENTION_ACTIVE,
  activeMention,

  MENTION_SELECT,
  selectMention,

  MENTION_PLACEHOLDER_ACTIVE,
  activePlaceholder,
}

export const MENTION_DEACTIVE = Symbol('MENTION_DEACTIVE');

export const MENTION_INSERT = Symbol('MENTION_INSERT');

export const MENTION_MULTIPLE_INSERT = Symbol('MENTION_MULTIPLE_INSERT');

export const MENTION_CANCEL_SELECT = Symbol('MENTION_CANCEL_SELECT');

export const MENTION_PLACEHOLDER_INSERT = Symbol('MENTION_PLACEHOLDER_INSERT');

export const MENTION_PLACEHOLDER_EXIT = Symbol('MENTION_PLACEHOLDER_EXIT');

export const MENTION_PLACEHOLDER_CANCEL = Symbol('MENTION_PLACEHOLDER_CANCEL');

export interface InsertMentionPayload {
  suggestion: Suggestion;
  // 替换 placeholder 插入人
  at?: string;
}

export interface MultipleInsertMentionPayload {
  suggestions: Suggestion[];
}

export function deactiveMention(): Action<{}> {
  return {
    type: MENTION_DEACTIVE,
    payload: {},
  };
}

export function insertMention(
  suggestion: Suggestion,
  at?: string,
): Action<InsertMentionPayload> {
  return {
    type: MENTION_INSERT,
    payload: { suggestion, at },
  };
}

export function insertMentions(
  suggestions: Suggestion[],
): Action<MultipleInsertMentionPayload> {
  return {
    type: MENTION_MULTIPLE_INSERT,
    payload: {
      suggestions,
    },
  };
}

export function cancelMentionSelect() {
  return {
    type: MENTION_CANCEL_SELECT,
    payload: {},
  };
}

export function insertPlaceholder() {
  return {
    type: MENTION_PLACEHOLDER_INSERT,
    payload: {},
  };
}

export function exitPlaceholder(text: string, node: Inline) {
  return {
    type: MENTION_PLACEHOLDER_EXIT,
    payload: {
      text,
      node,
    },
  };
}

export function cancelPlaceholder(key: string) {
  return {
    type: MENTION_PLACEHOLDER_CANCEL,
    payload: {
      key,
    },
  };
}

export const actions = {
  activeMention,
  deactiveMention,
  insertMention,
  selectMention,
  insertMentions,
  cancelMentionSelect,
  insertPlaceholder,
  exitPlaceholder,
  cancelPlaceholder,
  activePlaceholder,
}

export const isActions = {
  isActiveMention,
  isSelectMention,
  isActivePlaceholder,
}
